'use client';
import React from 'react';
import { useSession } from '@/features/auth/session';
import { useQuery } from '@tanstack/react-query';
import { listAgencies } from '@/services/agencies';
import { listClientsByAgency } from '@/services/clients';
import { Typography } from '@mui/material';
import { DataGrid, GridColDef } from '@mui/x-data-grid';

export default function DashboardPage() {
  const { session } = useSession();
  const isParent = session?.role === 'parent';
  const q = useQuery<any[]>({
    queryKey: ['dashboard', session?.role, session?.agencyId],
    queryFn: () => isParent ? listAgencies() : listClientsByAgency(session!.agencyId!),
  });
  if (!session) return null;
  if (!q.data) return null;

  const rows = q.data;
  const columns: GridColDef[] = isParent
    ? [{ field: 'name', headerName: 'Agency', flex: 1 }]
    : [
        { field: 'email', headerName: 'Email', flex: 1 },
        { field: 'firstName', headerName: 'First', width: 120 },
        { field: 'lastName', headerName: 'Last', width: 120 },
        { field: 'sport', headerName: 'Sport', width: 120 },
      ];

  return (
    <>
      <Typography variant="h4" gutterBottom>Dashboard</Typography>
      <div style={{ height: 520 }}>
        <DataGrid rows={rows as any[]} columns={columns} getRowId={(r)=>r.id} />
      </div>
    </>
  );
}


